
import {defineComponent} from 'vue';
import {IonFab, IonFabButton, IonIcon} from '@ionic/vue';
import {heartCircleOutline, rocketOutline, sendOutline, logOutOutline} from 'ionicons/icons'
import getRandomValue from "../fn/getRandomValue";
import DataTypes from "@/interfaces/DataTypes";
import StylesOutputType from "@/interfaces/StylesOutputType";
import LogMessageType from "@/interfaces/LogMessageType";

export default defineComponent({
  name: "MonsterGame",
  components: {
    IonIcon,
    IonFab,
    IonFabButton
  },
  data(): DataTypes {
    return {
      heartCircleOutline,
      rocketOutline,
      sendOutline,
      logOutOutline,
      playerHealth: 100,
      monsterHealth: 100,
      currentRound: 0,
      winner: null,
      logMessages: []
    };
  },
  computed: {
    monsterBarStyles(): StylesOutputType {
      if (this.monsterHealth < 0) {
        return {width: '0%'};
      }
      return {width: this.monsterHealth + '%'};
    },
    playerBarStyles(): StylesOutputType {
      if (this.playerHealth < 0) {
        return {width: '0%'};
      }
      return {width: this.playerHealth + '%'};
    },
    mayUseSpecialAttack(): boolean {
      return this.currentRound % 3 !== 0;
    },
  },
  watch: {
    playerHealth(value: number): void {
      if (value <= 0 && this.monsterHealth <= 0) {
        // A draw
        this.winner = 'draw';
      } else if (value <= 0) {
        // Player lost
        this.winner = 'monster';
      }
    },
    monsterHealth(value: number): void {
      if (value <= 0 && this.playerHealth <= 0) {
        // A draw
        this.winner = 'draw';
      } else if (value <= 0) {
        // Monster lost
        this.winner = 'player';
      }
    },
  },
  methods: {
    startGame(): void {
      this.playerHealth = 100;
      this.monsterHealth = 100;
      this.winner = null;
      this.currentRound = 0;
      this.logMessages = [];
    },
    attackMonster(): void {
      this.currentRound++;
      const attackValue = getRandomValue(5, 12);
      this.monsterHealth -= attackValue;
      this.addLogMessage('player', 'attack', attackValue);
      this.attackPlayer();
    },
    attackPlayer(): void {
      const attackValue = getRandomValue(8, 15);
      this.playerHealth -= attackValue;
      this.addLogMessage('monster', 'attack', attackValue);
    },
    specialAttackMonster(): void {
      this.currentRound++;
      const attackValue = getRandomValue(10, 25);
      this.monsterHealth -= attackValue;
      this.addLogMessage('player', 'attack', attackValue);
      this.attackPlayer();
    },
    healPlayer(): void {
      this.currentRound++;
      const healValue = getRandomValue(8, 20);
      if (this.playerHealth + healValue > 100) {
        this.playerHealth = 100;
      } else {
        this.playerHealth += healValue;
      }
      this.addLogMessage('player', 'heal', healValue);
      this.attackPlayer();
    },
    surrender(): void {
      this.winner = 'monster';
    },
    addLogMessage(who: string, what: string, value: number): void {
      const message: LogMessageType = {
        actionBy: who,
        actionType: what,
        actionValue: value
      }
      this.logMessages.unshift(message);
    }
  },
})
